// Types formats JSON : FormatX par convention
// Il est recommand de choisir le plus possible des formats immutables.
// Format ::= { (readonly etiquette : Format)*} 
//          | { (readonly etiquette : Format)*, (etiquette : Format)+, mutable : Unite} 
//          | { readonly table: { readonly [cle: string]: T }}
//          | { readonly table: { [cle: string]: T }}
//          | { table: { readonly [cle: string]: T }, mutable : Unite}
//          | { table: { [cle: string]: T }, mutable : Unite}

// Modle gnrique d'une enveloppe d'un tat
//
// TEX : type de sortie immutable (souvent format JSON en lecture seulement)
// TIN : type d'entre pour l'tat en format JSON ou non (mutable ou non, confin si mutable)
// E : tiquettes utiles pour une reprsentation (cf. mthode net)
//
// La diffrence entre TIN et TEX permet de grer les effets de bord sur l'tat, souvent au format JSON.
// Une fonction de conversion de TIN vers TEX est requise.
// Toute mthode ayant une occurrence positive de TIN est protected. En effet, elle peut permettre d'obtenir l'tat mutable
// et de raliser un effet de bord sur l'tat s'il est mutable.
// Cette classe abstraite doit tre tedue ;
// - implmentation de net et reprsenter,
// - extension par des mthodes modifiant ou observant l'tat.
// Srialisation
// C'est le type interne TIN qui est utilis pour la srilisation (via la mthode brut()). 
// En consquence, un format srialisable doit tre utilis : types primitifs, 
// et comme contructeurs de types les tableaux et les structures indexes.

// Enveloppe de l'tat qui est donc partag.
export abstract class Enveloppe<TIN, TEX, E extends string> {
    private structure: TIN;
    protected etatEnVal: (x: TIN) => TEX;
    constructor(etatEnVal: (x: TIN) => TEX, etat: TIN) {
        this.structure = etat;
        this.etatEnVal = etatEnVal;
    }
    protected etat(): TIN {
        return this.structure;
    }
    val(): TEX {
        return this.etatEnVal(this.structure);
    }
    // transformation brute du json de type TIN en string
    brut(): string {
        return JSON.stringify(this.structure);
    };
    // reprsentation dans un json simplifi
    abstract net(etiquette: E): string;
    abstract representation(): string;
}



